<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <MessageModal ref="message" />
  <h4></h4>
  <VForm ref="formRef" v-slot="{ errors, meta }" @submit="register">
    <div class="form-floating mb-3">
      <VField
        name="email"
        type="email"
        rules="required|email"
        class="form-control"
        v-model="email"
      />
      <label for="floatingInput"
        >Email address<ErrorMessage name="email" class="text-danger ms-3"
      /></label>
    </div>
    <div class="form-floating mb-3">
      <VField
        id="phone"
        name="phone"
        type="text"
        class="form-control"
        :class="{ 'is-invalid': errors[''] }"
        :rules="isPhone"
      />
      <label for="phone"
        >phone<error-message name="phone" class="text-danger ms-3"></error-message
      ></label>
    </div>
    <div class="form-floating mb-3">
      <VField name="password" class="form-control" type="password" rules="required|min:6" />
      <label for="floatingInput"
        >Password<ErrorMessage name="password" class="ms-3 text-danger"
      /></label>
    </div>
    <div class="input-group mb-3">
      <input
        type="text"
        class="form-control"
        placeholder="Birthday"
        disabled
        aria-label="Recipient's username"
        aria-describedby="basic-addon2"
      />
      <span class="input-group-text" id="basic-addon2" style="width: 70%"
        ><VField name="birth" class="form-control" type="text" rules="required" v-model="birth">
          <VueDatePicker
            placeholder="Birthday Day"
            format="yyyy-MM-dd"
            valueType="format"
            :enable-time-picker="false"
            :max-date="new Date()"
            v-model="birth"
          ></VueDatePicker> </VField
      ></span>
    </div>
    <div class="d-flex justify-content-between">
      <button
        type="submit"
        class="btn btn-primary"
        :class="{ 'btn-info': !meta.valid }"
        :disabled="!meta.valid"
      >
        
      </button>
    </div>
  </VForm>
</template>

<script>
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import emitter from '@/assets/javascript/emitter';
import MessageModal from '../models/MessageModal.vue';

export default {
  data() {
    return {
      email: '',
      phone: '',
      password: '',
      birth: '',
    };
  },
  components: { VueDatePicker, MessageModal },
  methods: {
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '';
    },
    register(e) {
      emitter.emit('loadingStatus', true);
      e.birth = this.$moment(e.birth).format('yyyy-MM-DD');
      const api = `${process.env.VUE_APP_API}/user/sign_up`;
      this.$http
        .post(api, e)
        .then((res) => {
          this.$refs.message.openModal(true, '');
          if (res.data.status) {
            this.$refs.formRef.resetForm();
          }
          const expries = new Date(Date.now() + 1000 * 60 * 30).toGMTString();
          document.cookie = `LULU-Bake=${res.data.data.token}; expires=${expries}; path=/`;
          this.$router.push('/');
        })
        .catch((error) => {
          this.$refs.message.openModal(false, error.response.data.message);
        });
      emitter.emit('loadingStatus', false);
    },
  },
};
</script>
