<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <MessageModal ref="message" />
    <h4></h4>
    <VForm ref="formRefSignin" v-slot="{ meta }" @submit="logIn">
      <div class="form-floating mb-3">
        <VField name="email" type="email" rules="required|email" class="form-control" />
        <label for="floatingInput"
          >Email address<ErrorMessage name="email" class="text-danger ms-3"
        /></label>
      </div>
      <div class="form-floating mb-3">
        <VField name="password" class="form-control" type="password" rules="required" />
        <label for="floatingInput"
          >Password<ErrorMessage name="password" class="ms-3 text-danger"
        /></label>
      </div>
      <button
        type="submit"
        class="btn btn-primary"
        :class="{ 'btn-info': !meta.valid }"
        :disabled="!meta.valid"
      >
        
      </button>
    </VForm>
</template>

<script>
import emitter from '@/assets/javascript/emitter';
import MessageModal from '../models/MessageModal.vue';

export default {
  data() {
    return {};
  },
  components: {
    MessageModal,
  },
  methods: {
    logIn(e) {
      emitter.emit('loadingStatus', true);
      const api = `${process.env.VUE_APP_API}/user/sign_in`;
      this.$http
        .post(api, e)
        .then((res) => {
          this.$refs.message.openModal(true, '');
          const expries = new Date(Date.now() + 1000 * 60 * 30).toGMTString();
          document.cookie = `LULU-Bake=${res.data.data.token}; expires=${expries}; path=/`;
          this.$router.push('/user/profile');
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
            this.$refs.message.openModal(false, error.response.data.message);
          }
        });
      emitter.emit('loadingStatus', false);
    },
  },
};
</script>
